import axios from 'axios';
import moment from 'moment';
import { Container, Accordion } from "react-bootstrap";
import { useContext, useEffect, useState } from 'react';
import Select from "react-select";
import { useHistory, useParams } from 'react-router'
import { Context } from '../context/Context'
import Swal from 'sweetalert2'

const PrayerDetails = () => {
  const { user, dispatch } = useContext(Context)
  const { id } = useParams()
  const [prayer, setPrayer] = useState(null)
  const [error, setError] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [selectedOption, setSelectedOption] = useState();
  const [evangelists, setEvangelists] = useState([]);
  const history = useHistory();

  useEffect(() => {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }

    axios
      .get(`https://evangelism.thecophq.org/api/prayers/${id}`, config)
      .then((result) => {
        setPrayer(result.data)
        // console.log(result.data);
        setError(null)
        setIsLoading(false)
      })
      .catch((err) => {
        // console.log("an error occured");
        setError('An error has been encountered, please try again later')
        setIsLoading(false)
      });

    axios.get("https://evangelism.thecophq.org/api/users",config)
        .then(result=>{
          let tempOption = [];
          result.data.forEach(element => {
            if(element.role.type === "authenticated"){
                const option = {label: element.username, value:element.id};
                tempOption.push(option);
            }
          });
          setEvangelists(tempOption);
        })
        .catch(err=>{
          console.log(err.message)
        })
  }, [id, user])

  const asignmentHandler = ()=>{
    // console.log(selectedOption.value);
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios.put(`https://evangelism.thecophq.org/api/prayers/${prayer.id}`,{evangelist:selectedOption.value},config)
        .then(result=>{
          Swal.fire({
            title: 'Suucessful',
            text: `The prayer has been asigned to ${selectedOption.label}`,
            icon: 'success',
            showCancelButton:false,
            // timer:2000
          }).then(response=>{
            // console.log(response);
            if(response.isConfirmed){
                history.goBack();
            }
          })
        })
        .catch(err=>{
          Swal.fire({
            title: 'Error',
            text: 'Prayer asignment has not gone through, try again',
            icon: 'error',
            showCancelButton:false,
          });
        })

  }

  return (
    <Container className="mt-5">
      {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {prayer && (
        <div className="pt-5">
          <div
            className="card pt-2"
            style={{ width: '22rem', marginLeft: '30%' }}
          >
            <div className="pt-3 text-center">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="currentColor"
                className="bi bi-person-circle"
                viewBox="0 0 16 16"
                style={{ color: '#a8aeac' }}
              >
                <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
                <path
                  fillRule="evenodd"
                  d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"
                />
              </svg>
            </div>
            <div className="card-body">
              {/* <h5 className="card-title">Prayer request details</h5> */}
              <div className="py-2 " style={{ backgroundColor: '#f5f4ee' }}>
                <div
                  className="px-3 text-align-start"
                  style={{ display: 'inline-block' }}
                >
                  Requester name:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {prayer.convert_name}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f9fafa' }}>
                <div
                  className="px-3 text-align-start"
                  style={{ display: 'inline-block' }}
                >
                  Requester phone:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {prayer.phone}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f5f4ee' }}>
                <div className="px-3 " style={{ display: 'inline-block' }}>
                  Prayer title:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {prayer.title}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f9fafa' }}>
                <div className="px-3 " style={{ display: 'inline-block' }}>
                  Date of request:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {moment(prayer.createdAt).format('YYYY-MM-DD')}
                </div>
              </div>
              <div className="py-2">
                <div className="px-3 pt-3">
                  <p className="card-text">{prayer.description}</p>
                </div>
              </div>
            </div>
            <hr/>
            <div >
            <Accordion defaultActiveKey="0" className="p-1">
            <Accordion.Item>
              <Select
                id="select-second-item"
                value={selectedOption}
                onChange={(value) => {
                  setSelectedOption(value);
                }}
                options={evangelists}
                placeholder="Select an evangelist to asign the prayer"
                isClearable={false}
              />
            </Accordion.Item>
          </Accordion>
            </div>

            <div className="card-body text-center">
              {selectedOption && <button type="button" className="btn btn-outline-info" onClick={asignmentHandler}>
                  Asign the prayer 
                </button>}
            </div>
          </div>
        </div>
      )}
    </Container>
  )
}

export default PrayerDetails
