import axios from 'axios'
import moment from 'moment'
import { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { Context } from '../context/Context'
const $ = require('jquery')

const TableComponent = (props) => {
  const [data, setData] = useState(props.data)
  const title = props.title
  const { user, dispatch } = useContext(Context)

  useEffect(() => {
    $(document).ready(function () {
      $('#example').DataTable()
    })
  }, [data, title])

  //update table content after removing a user
  const updateTable = (id) => {
    // for(let i =0; i< data.length; i++){
    //     if(data[i]._id === id){
    //         // console.log("before delete ", data.length)
    //        const newData =  data.splice(i, 1);
    //        setData(data);
    //     // console.log("after delete",data.length);
    //     }
    // }
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios
      .get('https://evangelism.thecophq.org/api/users', config)
      .then((result) => {
        setData(result.data)
      })
  }
  const handleDelete = (id, url) => {
    // console.log("id",id);
    const base_url = `https://evangelism.thecophq.org/api/${url}/${id}`
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios
      .delete(base_url, config)
      .then((result) => {
        updateTable(id)
      })
      .catch((err) => {
        console.log(err.message)
      })
    // console.log(data)
  }
  return (
    <>
      {title === 'Users' && (
        <table id="example" className="display px-1">
          <thead>
            <tr>
              <th>Number</th>
              <th>Full Name</th>
              <th>Username</th>
              <th>Email</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {data.map((value, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>
                    {value.first_name} {value.last_name}
                  </td>
                  <td>{value.username}</td>
                  <td>{value.email}</td>
                  <td>
                    <button
                      type="button"
                      className="btn btn-outline-danger btn-sm"
                      disabled
                      onClick={() => {
                        handleDelete(value.id, 'users')
                      }}
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      )}

      {title === 'Converts' && (
        <table id="example" className="display px-1">
          <thead>
            <tr>
              <th>Number</th>
              <th>Full Name</th>
              <th>District</th>
              <th>Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {data.map((value, index) => {
              if(value.district !== undefined){
                return (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>
                      {value.first_name} {value.last_name}
                    </td>
                    <td>{value.district.name}</td>
                    <td>{value.date_won}</td>
  
                    <td>
                      <Link to={`/admin/user-details/${value.id}`}>
                        <button
                          type="button"
                          className="btn btn-outline-info btn-sm"
                        >
                          Details
                        </button>
                      </Link>
                    </td>
                  </tr>
                )
              }  
            })}
          </tbody>
        </table>
      )}

      {title === 'Prayers' && (
        <table id="example" className="display px-1">
          <thead>
            <tr>
              <th>Number</th>
              <th>Name</th>
              <th>Title</th>
              <th>Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {data.map((value, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{value.convert_name}</td>
                  <td>{value.title}</td>
                  <td>{moment(value.createdAt).format('YYYY-MM-DD')}</td>

                  <td>
                    <Link to={`/admin/prayer-details/${value.id}`}>
                      <button
                        type="button"
                        className="btn btn-outline-info btn-sm"
                      >
                        Details
                      </button>
                    </Link>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      )}

      {title === 'Devotions' && (
        <table id="example" className="display px-1">
          <thead>
            <tr>
              <th>Number</th>
              <th>Topic</th>
              <th>Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {data.map((value, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{value.topic}</td>
                  <td>{value.date}</td>
                  {/* <td>{moment(value.date).format('YYYY-MM-YY')}</td> */}

                  <td>
                    <Link to={`/admin/devotion-details/${value.id}`}>
                      <button
                        type="button"
                        className="btn btn-outline-info btn-sm"
                      >
                        Details Devotion
                      </button>
                    </Link>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      )}
    </>
  )
}

export default TableComponent
