import axios from 'axios'
import { useContext, useRef, useState } from 'react'
import '../assets/css/login.css'
import { Context } from '../context/Context'
// import { Link } from 'react-router-dom';
import Swal from 'sweetalert2'
import "./forms.css"

function Login() {
  const userRef = useRef()
  const passwordRef = useRef()
  const { dispatch, isFetching } = useContext(Context)
  const [typeText, setTypeText] = useState('password')
  const [error, setError] = useState(null)
  const [showSpinner, setShowSpinner] = useState(false)

  const handleSubmit = async (e) => {
    e.preventDefault()
    setShowSpinner(true)
    dispatch({ type: 'LOGIN_START' })
    try {
      const res = await axios.post(
        'https://evangelism.thecophq.org/api/auth/local',
        {
          identifier: userRef.current.value,
          password: passwordRef.current.value,
        }
      )
      // console.log(res.data.user)
      dispatch({ type: 'LOGIN_SUCCESS', payload: res.data })
      // setError(null)
      // console.log("success login");
    } catch (err) {
      dispatch({ type: 'LOGIN_FAILURE' })
      // console.log("cannot logged in")
      Swal.fire({
        title: 'Error!',
        text: 'Login failed, Check your credencials and try again',
        icon: 'error',
      });
      setError('An error has occurred, please try again later')
      setShowSpinner(false)
    }
  }

  const handlePassword = (e) => {
    e.preventDefault()
    if (typeText === 'password') {
      setTypeText('text')
    } else if (typeText === 'text') {
      setTypeText('password')
    }
  }

  return (
    <div style={{ marginTop: '10rem' }}>
      <div className="card card-form mx-auto">
        <div className="card-body">
          <h5 className="card-title text-center fw-bold pb-2">COP Evangelism</h5>
          <h6 className="card-title text-center ">Sign In Form</h6>
          <form className="loginForm" onSubmit={handleSubmit}>
            <div className="my-2">
            <input
              className="form-control py-2"
              type="text"
              placeholder="Enter your username..."
              ref={userRef}
              required
            />
            </div>
            <div className="my-2">
            <div className="input-group mb-3">
              <input
                type={`${typeText}`}
                className="form-control"
                placeholder="Enter your password..."
                ref={passwordRef}
                aria-describedby="basic-addon2"
                required
              />
              <button
                className="input-group-text"
                id="basic-addon2"
                onClick={(e) => {
                  handlePassword(e)
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  className="bi bi-eye"
                  viewBox="0 0 16 16"
                >
                  <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8zM1.173 8a13.133 13.133 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5c2.12 0 3.879 1.168 5.168 2.457A13.133 13.133 0 0 1 14.828 8c-.058.087-.122.183-.195.288-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5c-2.12 0-3.879-1.168-5.168-2.457A13.134 13.134 0 0 1 1.172 8z" />
                  <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5zM4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0z" />
                </svg>
              </button>
            </div>
            </div>
            
            
            {/* {error && <span className="text-danger" role="alert">{error}</span>} */}
            {/* <input className="loginInput" type="password" placeholder="Enter your password..." ref={ passwordRef }/> */}
            <button className="btn btn-info text-light" type="submit">
              <span
                className="spinner-border spinner-border-sm"
                hidden={!showSpinner}
                id="spinner"
                role="status"
                aria-hidden="true"
              ></span>
              <span hidden={!showSpinner} id="loading">
                Loading...
              </span>
              <span id="login" hidden={showSpinner}>
                Login
              </span>
            </button>
            {/* <button
              className="btn btn-info"
              type="submit"
              disabled={isFetching}
            >
              Login
            </button> */}
            {/* <div className="text-center"><span>Do not have an account? </span><Link className="ps-3" to="/admin/register">SignUp here</Link></div> */}
          </form>
          {/* <button className="loginRegisterButton" type="submit"><Link className="link" to="/register">Register</Link></button> */}
        </div>
      </div>
    </div>
  )
}

export default Login
