import axios from "axios";
import { useEffect, useContext, useState } from "react";
import { Context } from '../context/Context'
import { Link } from 'react-router-dom'
import Swal from 'sweetalert2';

const Anouncement = ()=>{
    const { user, dispatch } = useContext(Context)
    const [data, setData]= useState([]);
    const [error, setError] = useState(null);
    const [reload, setReload] = useState(null)
    
    useEffect(()=>{
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
        }

        axios.get("https://evangelism.thecophq.org/api/anouncements", config)
            .then(result=>{setData(result.data)})
            .catch(err=>{setError("An error has been encountered, please try again later")});
    },[reload])

    const handleDelete = (idAnouncement)=>{
      const config = {
        headers: { Authorization: `Bearer ${user.jwt}` },
    }

    axios.delete(`https://evangelism.thecophq.org/api/anouncements/${idAnouncement}`, config)
        .then(result=>{
          console.log(result.data)
          Swal.fire({
            title: 'Suucessful',
            text: 'Anouncement removed successfully',
            icon: 'success',
            showCancelButton:false,
            timer:2000
          });
          setReload(result.data);
        })
        .catch(err=>{
          Swal.fire({
            title: 'Error',
            text: 'Anouncement was not deleted',
            icon: 'error',
            showCancelButton:false,
            timer:2000
          });
        });
    }

    return(
        <div className="anouncement" style={{marginTop:"6rem"}}>
            <div className="mx-2">
                <Link to={`/admin/edit-anouncement`}> <button  className="btn btn-outline-info">Create Announcement</button> </Link>
            </div>
            <div className="card p-2 m-2">
                  <div className="card-header">
                    <h3>List of Announcements</h3>
                  </div>
                  <table className="table table-striped m-1">
                    <thead className="border-top">
                      <tr>
                        {/* <th scope="col">Number</th> */}
                        <th scope="col">Number</th>
                        <th scope="col">Title</th>
                        <th scope="col">Author</th>
                        <th scope="col">Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {data.map((item,index)=>{
                          return(
                              <tr key={index}>
                                  <td>{index + 1}</td>
                                  <td>{item.title}</td>
                                  <td>{item.author}</td>
                                  <td>
                                    <Link to={`/admin/anouncement-details/${item.id}`}>
                                  <button
                                    type="button"
                                    className="btn btn-outline-info btn-sm mx-2"
                                    >
                                    Details
                                
                                    </button>
                                    </Link>
                                    
                                    <button
                                    type="button"
                                    className="btn btn-outline-danger btn-sm mx-2"
                                    onClick={()=>{
                                      handleDelete(item.id);
                                    }}
                                    >
                                    Remove
                                    </button>
                                  </td>
                              </tr>
                          );
                      })}
                    </tbody>
                  </table>
                </div>
        </div>
    );
}

export default Anouncement;