import './settings.css';
import axios from 'axios';
import { useContext, useState  } from 'react';
import { Context } from '../../context/Context';


function Settings() {
  
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [success, setSuccess] = useState(false);

  const { user, dispatch } = useContext(Context);
    
      const handleSubmit = async (e) => {
        e.preventDefault();
        dispatch({ type: "UPDATE_START" });
        const updatedUser = {
          userId: user._id,
          username,
          email,
          password,
        };
        try {
          const res = await axios.put("/users/" + user._id, updatedUser);
          setSuccess(true);
          dispatch({ type: "UPDATE_SUCCESS", payload: res.data });
        } catch (err) {
          dispatch({ type: "UPDATE_FAILURE" });
        }
      };
      
      
        return (
        <div className="login">
          <span className="loginTitle">Update Your Credentials</span>
            <form className="loginForm" onSubmit={handleSubmit}>
            <label>Username</label>
              <input className="loginInput"
                type="text" placeholder="Enter new email (optional)"
                onChange={(e) => setUsername(e.target.value)}
              />
            <label>Email</label>
              <input className="loginInput"
                type="email"
                placeholder="Enter new username (optional)"
                onChange={(e) => setEmail(e.target.value)}/>
            <label>* Password</label>
              <input className="loginInput"
                type="password" placeholder="Enter new password..."
                onChange={(e) => setPassword(e.target.value)}/>
            <button className="loginButton" type="submit" >Login</button>
          </form>
            {/* <button className="loginRegisterButton" type="submit"><Link className="link" to="/register">Register</Link></button> */}
        </div>
        
    
    )
}

export default Settings
