import { useEffect, useState } from 'react';
import { Bar} from 'react-chartjs-2';

const GraphDashboard = ({data, area})=>{
    const [labels, setLabels] = useState(null);
    const [dataUse, setDataUse] = useState([]);

    useEffect(()=>{
        setGraph();
        setPeriod();
    },[]);

    const setPeriod = ()=>{
        let convets = [];
        for(let item of data){
            const date = new Date(item.date_won);
            //get the month of the converts creation
           let d = date.getMonth();
            // console.log(d)
            d = convertDateOrDay(d);
            convets.push(d);
        }
        // console.log(convets)
        graphFinalData(convets);
    }

    const graphFinalData = (legend)=>{
        let tempData = [];

        //variabkes of months combination
        let countJan= 0;
        let countFeb = 0;
        let countMarch = 0;
        let countApr= 0;
        let countMay = 0;
        let countJun = 0;
        let countJul = 0;
        let countAug = 0;
        let countSep = 0;
        let countOct= 0;
        let countNov = 0;
        let countDec = 0;
        
        for(let item of legend){
            if(item === "January"){
                countJan++;
            }else if(item === "February"){
                countFeb++;
            }else if(item === "March"){
                countMarch++;
            }else if(item === "April"){
                countApr++;
            }else if(item === "May"){
                countMay++;
            }else if(item === "June"){
                countJun++;
            }else if(item === "July"){
                countJul++;
            }else if(item === "August"){
                countAug++;
            }else if(item === "September"){
                countSep++;
            }else if(item === "Octomber"){
                countOct++;
            }else if(item === "November"){
                countNov++;
            }else if(item === "December"){
                countDec++;
            }
        }; 
        tempData.push(countJan);tempData.push(countFeb);tempData.push(countMarch); tempData.push(countApr); tempData.push(countMay);
        tempData.push(countJun); tempData.push(countJul);tempData.push(countAug); tempData.push(countSep);
        tempData.push(countOct);tempData.push(countNov); tempData.push(countDec);
           
        setDataUse(tempData);
    }

    const convertDateOrDay = (param)=>{
        switch(param){
            case 0 : return "January";
            case 1 : return "February";
            case 2 : return "March";
            case 3 : return "April";
            case 4 : return "May";
            case 5 : return "June";
            case 6 : return "July";
            case 7 : return "August";
            case 8 : return "September";
            case 9 : return "Octomber";
            case 10 : return "November";
            case 11 : return "December";
        }
    }
    const setGraph = ()=>{
        setLabels(["January","Febriary","March","April","May","June","July","August","September","October","November","December"]);
    }
    return(
        <Bar
          data={{
            labels: labels,
            datasets: [
              {
                label: area,
                data: dataUse,
                backgroundColor: [
                  "rgba(24, 242, 147)",
                  "rgb(155, 24, 242)",
                  "rgb(242, 24, 87)",
                  "rgb(24, 242, 104)",
                  "rgb(210, 35, 232)",
                  "rgb(168, 35, 232)",
                  "rgb(208, 233, 34)",
                  "rgb(132, 143, 148)",
                  "rgb(44, 222, 216)",
                  "rgb(222, 45, 192)",
                  "rgb(23, 7, 54)",
                  "rgb(242, 210, 24)",
                ],
                borderWidth: 1,
              },
              
            ],
          }}
          width={400}
          height={300}
          options={{
            maintainAspectRatio: false,
          }}
        />
    );
}

export default GraphDashboard;