import { useRef, useContext } from "react";
import { Context } from '../context/Context';
import axios from 'axios';
import { useHistory } from "react-router-dom";
import Swal from 'sweetalert2'

const DevotionForm = () => {
    const { user, dispatch } = useContext(Context)
    const topicRef = useRef();
    const memoryRef = useRef();
    const mainRef = useRef();
    const dateRef = useRef();
    const introductionRef = useRef();
    const questionRef = useRef();
    const conclusionRef = useRef();
    const history = useHistory();

    const handleCreation = (e)=>{
        e.preventDefault();
        const devotion = {
            date:dateRef.current.value,
            topic:topicRef.current.value,
            main_text:mainRef.current.value,
            memory_verse:memoryRef.current.value,
            introduction:introductionRef.current.value,
            questions:questionRef.current.value,
            conclusion:conclusionRef.current.value,
        };

        // console.log(devotion);

        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
          }

          axios
        .post('https://evangelism.thecophq.org/api/devotions', devotion, config)
        .then((result) => {
        //    console.log(result.data)
        Swal.fire({
          title: 'Suucessful',
          text: 'Devotion created successfully',
          icon: 'success',
          showCancelButton:false,
        }).then(response=>{
          if(response.isConfirmed){
            history.goBack();
          }
        })
        })
        .catch((err) => {
          Swal.fire({
            title: 'Error',
            text: 'Registration failled, try again latter',
            icon: 'error',
            showCancelButton:false,
          });
        })
    }
  return (
    <div className="p-3" style={{ marginTop: "7rem" }}>
      <div className="card mx-auto" style={{ width: "90%" }}>
        <div className="card-body ">
          <h5 className="card-title text-center fw-bold">
            Devotion Registration
          </h5>
          <form>
            <label htmlFor="topic" className="form-label">
              Topic
            </label>
            <input type="text" className="form-control mb-3" id="topic" required ref={topicRef}/>

            <label htmlFor="memory" className="form-label">
              memory
            </label>
            <input type="text" className="form-control mb-3" id="memory" required ref={memoryRef}/>

            <label htmlFor="date" className="form-label">
              Date
            </label>
            <input type="date" className="form-control mb-3" id="date" required ref={dateRef} />

            <label htmlFor="main-text" className="form-label">
              Main Text
            </label>
            <input type="text" className="form-control mb-3" id="main-text" required ref={mainRef} />

            <label htmlFor="introduction" className="form-label">
              Introduction
            </label>
            <textarea
              className="form-control mb-3"
              id="introduction"
              required
              ref={introductionRef}
            ></textarea>

            <label htmlFor="question" className="form-label">
              Questions
            </label>
            <textarea
              className="form-control mb-3"
              id="question"
              required
              ref={questionRef}
            ></textarea>

            <label htmlFor="conclusion" className="form-label">
              Conclusion
            </label>
            <textarea
              className="form-control mb-3"
              id="conclusion"
              required
              ref={conclusionRef}
            ></textarea>
            <div>
            <button className="btn btn-info text-light" style={{width:"100%"}} onClick={handleCreation}>Register</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default DevotionForm;
