// reactstrap components
import axios from 'axios'
import { Context } from '../context/Context'
import React, { useContext, useEffect, useState } from 'react'
import { useLocation } from 'react-router'
import 'react-table/react-table.css'
import PrayerComponent from './PrayerComponent'

function Converts() {
  const { user, dispatch } = useContext(Context)
  const [converts, setConverts] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)
  const { search } = useLocation()

  // console.log(search)

  //fetch posts
  useEffect(() => {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios
      .get('https://evangelism.thecophq.org/api/reports/converts', config)
      .then((result) => {
        setConverts(result.data)
        setError(null)
        setIsLoading(false)
      })
      .catch((err) => {
        setError('An error has been encountered, please try again later')
        setIsLoading(false)
      })
    // setIsLoading(true)
  }, [search])

  return (
    <div className="content">
      {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '90px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {converts && !isLoading && (
        <PrayerComponent data={converts} title="Converts" />
      )}
    </div>
  )
}

export default Converts
