import axios from 'axios'
import { useEffect, useState, useContext } from 'react'
import { useParams } from 'react-router'
import { Context } from "../context/Context";

const DevotionDetails = () => {
  const { devotionId } = useParams()
  const [data, setData] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)
  const { user, dispatch } = useContext(Context);

  useEffect(() => {
    // console.log(devotionId)
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` }
   };
    axios
      .get(`https://evangelism.thecophq.org/api/devotions/${devotionId}`, config)
      .then((result) => {
        // console.log(result.data)
        setData(result.data)
        setError(null)
        setIsLoading(false)
      })
      .catch((err) => {
        // console.log("error")
        setError('An error occurred, please try again later')
        setIsLoading(false)
      })
  }, [devotionId])

  return (
    <div className="mx-5" style={{ marginTop: '6rem' }}>
      {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {/* {console.log(data)} */}
      {data && (
        <div className="p-1">
          <h6 className="text-info p-1">Date of this devotion: {data.date}</h6>
          <h5 className="text-center">{data.topic}</h5>
          <div>
            <h6 className="text-center mt-1">Introduction</h6>
            <p>{data.introduction}</p>
          </div>
          <div>
            <h6 className="text-center mt-1">Questions</h6>
            <p>{data.questions}</p>
          </div>
          <div>
            <h6 className="text-center mt-1">Conclusion</h6>
            <p>{data.conclusion}</p>
          </div>
        </div>
      )}
    </div>
  )
}

export default DevotionDetails
