
import { useRef, useContext, useEffect, useState } from "react";
import { Context } from '../context/Context';
import axios from 'axios';
import { useHistory } from "react-router-dom";
import Swal from 'sweetalert2';
import { useParams } from "react-router";

const AnouncementForm = ()=>{
    const { user, dispatch } = useContext(Context)
    const titleRef = useRef();
    const authorRef = useRef();
    const dateRef = useRef();
    const descriptionRef = useRef();
    const history = useHistory();
    const { id } = useParams();

    const [anouncement, setAnouncement] = useState(null);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    const [title, setTitle] = useState(null);
    const [author, setAuthor] = useState(null);
    const [date, setDate] = useState(null);

    useEffect(()=>{
      if(id){
        const config = {
          headers: { Authorization: `Bearer ${user.jwt}` },
        };
    
        axios
          .get(`https://evangelism.thecophq.org/api/anouncements/${id}`, config)
          .then((result) => {
            //   console.log(result.data)
            setAnouncement(result.data)
            setTitle(result.data.title);
            setAuthor(result.data.author);
            // setIsLoading(false);
          })
          .catch((err) => {
            setError("An error has been encounted, please try again later");
            setIsLoading(false);
          });
      }
    });

    const handleUpdate = (e)=>{
      e.preventDefault();
      const newAnouncement = {
        title:titleRef.current.value,
        author:authorRef.current.value,
        date:dateRef.current.value,
    };
    console.log(newAnouncement)
    }

    const handleCreation = (e)=>{
        e.preventDefault();
        const anouncement = {
            title:titleRef.current.value,
            author:authorRef.current.value,
            description:descriptionRef.current.value,
        };

        // console.log(devotion);

        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
          }

          axios
        .post('https://evangelism.thecophq.org/api/anouncements', anouncement, config)
        .then((result) => {
        //    console.log(result.data)
        Swal.fire({
          title: 'Suucessful',
          text: 'Anouncement created successfully',
          icon: 'success',
          showCancelButton:false,
          timer:2000
        });
              history.push('/');
        })
        .catch((err) => {
          Swal.fire({
            title: 'Error',
            text: 'Registration failled, try again latter',
            icon: 'error',
            showCancelButton:false,
            timer:2000
          });
        })
    }
    return(
        <div className="edit-anoun" style={{marginTop:"6rem"}}>
            <div className="card mx-auto" style={{ width: "90%" }}>
              {(!id) ?
                    <div className="card-body ">
                    <h5 className="card-title text-center fw-bold">
                      Announcement Registration
                    </h5>
                    <form>
                      <label htmlFor="topic" className="form-label">
                        Title
                      </label>
                      <input type="text" className="form-control mb-3" id="topic" required ref={titleRef}/>
          
                      <label htmlFor="memory" className="form-label">
                        Author
                      </label>
                      <input type="text" className="form-control mb-3" id="memory" required ref={authorRef}/>
          
                      <label htmlFor="date" className="form-label">
                        Date
                      </label>
                      <input type="date" className="form-control mb-3" id="date" required ref={dateRef} />
          
                      <label htmlFor="conclusion" className="form-label">
                        Description
                      </label>
                      <textarea
                        className="form-control mb-3"
                        id="conclusion"
                        required
                        ref={descriptionRef}
                      ></textarea>
                      <div>
                      <button className="btn btn-info text-light" style={{width:"100%"}} onClick={handleCreation}>Register</button>
                      </div>
                    </form>
                  </div>
                  :
                  <>
                    {anouncement && <div className="card-body ">
                    <h5 className="card-title text-center fw-bold">
                      Announcement Update
                    </h5>
                    <form>
                      <label htmlFor="topic" className="form-label">
                        Title
                      </label>
                      <input type="text" className="form-control mb-3" id="topic" defaultValue={anouncement.title} required ref={titleRef} />
          
                      <label htmlFor="memory" className="form-label">
                        Author
                      </label>
                      <input type="text" className="form-control mb-3" id="memory" defaultValue={anouncement.author} required ref={authorRef} />
          
                      <label htmlFor="date" className="form-label">
                        Date
                      </label>
                      <input type="date" className="form-control mb-3" id="date"  required ref={dateRef} />
                      <div>
                      <button className="btn btn-info text-light" style={{width:"100%"}} onClick={handleUpdate}>Update</button>
                      </div>
                    </form>
                  </div>}
                  </>}
            </div>
        </div>
    );
}

export default AnouncementForm;