import { useState } from "react";

import { CSVReader } from "react-papaparse";
// import SendData from "components/SendData";


const FileReader = (props) => {
  const [dataFromFile, setDataFromFile] = useState(null);
  const [error, setError] = useState(null);

  //extract data from the file once its loaded
  const getData = (fileData) => {
    props.dataHandler(fileData);
  };

  const handleOnDrop = (data) => {
    setDataFromFile(data);
    getData(data);
    // handleData(data);
  };

  const handleOnError = (err, file, inputElem, reason) => {
    setError("Error occured");
    console.log(err);
  };

  const handleOnRemoveFile = (data) => {
    setDataFromFile(data);
  };

  return (
    <CSVReader
      onDrop={(fileData) => {
        // console.log(fileData)
        handleOnDrop(fileData);
      }}
      onError={(err) => {
        handleOnError(err);
      }}
      addRemoveButton
      removeButtonColor="#659cef"
      onRemoveFile={(fileData) => {
        handleOnRemoveFile(fileData);
      }}
    >
      <span>Drop your CSV file here or click to upload.</span>
    </CSVReader>
  );
};

export default FileReader;
