import axios from 'axios'
import TableComponent from 'components/TabbleComponent'
import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { Context } from '../context/Context'
import FileReader from './FileReader'

const Devotions = () => {
  const { user, dispatch } = useContext(Context)
  const [data, setData] = useState(null)
  const [error, setError] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [reload, setReload] = useState(false)
  const [showButton, setShowButton] = useState(false)
  const [fileData, setFileData] = useState(null)

  // console.log("inside devotions");

  useEffect(() => {
    // console.log("inside useEffect devotion")

    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };

    axios
      .get('https://evangelism.thecophq.org/api/devotions', config)
      .then((result) => {
        // console.log("devotions call");
        setData(result.data)
        setError(null)
        setIsLoading(false)
      })
      .catch((err) => {
        setError('An error has been encountered, please try again later')
        setIsLoading(false)
      })
  }, [user, reload])

  const getData = (dat) => {
    if (dat) {
      // console.log("data after selection ",dat);
      setFileData(dat)
      setShowButton(true)
      // handleData(dat)
    }
  }

  const handleData = (data1) => {
    let temp = []
    // console.log(data1)
    data1.forEach((item, index) => {
      if (index !== 0) {
        const obj = {
          date: item.data[0],
          topic: item.data[1],
          main_text: item.data[2],
          memory_verse: item.data[3],
          introduction: item.data[4],
          questions: item.data[5],
          conclusion: item.data[6],
        }
        temp.push(obj)
      }
      // console.log(item.data);
    })

    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    temp.forEach((item) => {
      // const dataJson = JSON.stringify(item);
      // console.log(item)
      axios
        .post('https://evangelism.thecophq.org/api/devotions', item, config)
        .then((result) => {
          // console.log(result.data)
        })
        .catch((err) => {
          // console.log(err.message)
        })
    })
  }

  return (
    <div style={{ marginTop: '8rem' }}>
      <div className="p-3">
        <div className="mx-2">
         <Link to={`/admin/edit-devotion`}> <button  className="btn btn-outline-info">Create Devotion</button> </Link>
        </div>
        <FileReader dataHandler={getData} />
        {/* {console.log('HELLO')} */}
        <div>
          {showButton && (
            <button
              type="button"
              className="btn btn-outline-info mx-5"
              onClick={() => {
                handleData(fileData);
                setReload(!reload)
                setShowButton(false);
                
              }}
              style={{ width: '90%' }}
            >
              Post your csv file
            </button>
          )}
        </div>
      </div>
      <div className="px-5">
        {error && (
          <div className="alert alert-danger" role="alert">
            <p>{error}</p>
          </div>
        )}
        {isLoading && (
          <div className="text-center">
            <div
              className="spinner-border text-info "
              role="status"
              style={{ marginTop: '60px' }}
            >
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>
        )}
        {data && <TableComponent data={data} title="Devotions" />}
      </div>
    </div>
  )
}

export default Devotions
