
import axios from "axios";
import { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import { useParams } from "react-router";
import { useContext } from "react";
import { Context } from "../context/Context";
import { Link } from "react-router-dom";
import moment from "moment";

const AnouncemetDetails = ()=>{
    const { user, dispatch } = useContext(Context);
    const { id } = useParams();
    const [anouncement, setAnouncement] = useState(null);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        // console.log(id);
        const config = {
          headers: { Authorization: `Bearer ${user.jwt}` },
        };
    
        axios
          .get(`https://evangelism.thecophq.org/api/anouncements/${id}`, config)
          .then((result) => {
            //   console.log(result.data)
            setAnouncement(result.data);
            setError(null);
            setIsLoading(false);
          })
          .catch((err) => {
            setError("An error has been encounted, please try again later");
            setIsLoading(false);
          });
    
      },[id]);
      return(
      <Container className="mt-5">
      {error && (<div className="alert alert-danger" role="alert">
                <p>{error}</p>
      </div>)}
      {isLoading && <div className="text-center">
        <div className="spinner-border text-info " role="status" style={{marginTop:"60px"}}>
          <span className="visually-hidden">Loading...</span>
        </div>
        </div>}
      {anouncement && (
        <div className="pt-5" >
          <div className="card pt-2 mx-auto" style={{width: "30rem"}}>
            <div className="card-body">
              <h5 className="card-title p-1">Details of Announcement</h5>
              <div className="py-2 " style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>Title:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{anouncement.title}</div>
              </div>
              <div className="py-2 " style={{backgroundColor:"#f9fafa"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>Author:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{anouncement.author}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 " style={{"display":"inline-block"}}>Date:</div>
                {/* <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{moment(anouncement.date).format('YYYY-MM-DD')}</div> */}
              </div>
              
            </div>
            
            <div className="card-body">
                <p className="fw-bold text-center">Description:</p>
                <p>{anouncement.description}</p>
             
            </div>
          </div>
          <p className="text-center">
             <Link to="/admin/anouncements" className="card-link">
                  <button type="button" class="btn btn-outline-info">Go back to Announcement page</button>
              </Link>
          </p>
        </div>
      )} 
    </Container>
      );
}

export default AnouncemetDetails;