import React from "react";


// reactstrap components
import '../assets/css/register.css'

import axios from "axios";
import { useEffect, useState } from "react";
import { useLocation } from "react-router";
import 'react-table/react-table.css';
import PrayerComponent from "./PrayerComponent";
import { useContext } from "react";
import { Context } from "../context/Context";
import { Link } from "react-router-dom";


function Users() {
  const { user, dispatch } = useContext(Context);
  // const [error, setError] = useState(false);
  const [users, setUsers] = useState([]);
  // const [isLoading, setIsLoading] = useState(false);
  const {search} = useLocation();
  // const [showRegister, setShowRegister] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  // const [data, setData] = useState(null);
  const [error, setError] = useState(null);

  // console.log(search)

    //fetch posts
  useEffect(() => {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` }
    };
    axios.get("https://evangelism.thecophq.org/api/users", config)
          .then(result=>{
              setUsers(result.data);
              // console.log(result.data);
              setError(null);
              setIsLoading(false);
          })
          .catch(err=>{
            setError("An error has been encountered, please try again later");
            setIsLoading(false);
          });
    }, [search]);
  
  return (
    <div className="content">
        <div>
          <Link to="/admin/register" className=""><button type="button" className="btn btn-info">Create A New User</button></Link>
        </div>
        {error && (<div className="alert alert-danger" role="alert">
                <p>{error}</p>
      </div>)}
      {isLoading && <div className="text-center">
        <div className="spinner-border text-info " role="status" style={{marginTop:"90px"}}>
          <span className="visually-hidden">Loading...</span>
        </div>
        </div>}
        {(users && !isLoading) && <PrayerComponent data={users} title="Users"/>}
      </div>
      );
  
}

export default Users;