import { Bar, Line} from 'react-chartjs-2';

const GraphComponent = (props)=>{
    const labels = props.labels;
    const dataUse = props.dataUse;
    const area = props.area;


    return(
      <>
        {(props.area1 && props.dataUse1) ? <Bar
          data={{
            labels: labels,
            datasets: [
              {
                label: `# ${area}`,
                data: dataUse,
                backgroundColor:"rgb(132, 143, 148)",
                borderWidth: 1,
              },
              {
                label: `# ${props.area1}`,
                data: props.dataUse1,
                backgroundColor:  "rgb( 75, 192, 192)",
                borderWidth: 1
              },
            ],
          }}
          width={400}
          height={200}
          options={{
            maintainAspectRatio: false,
          }}
        /> : <Line
        data={{
          labels: labels,
          datasets: [
            {
              label: "# of Converts",
              data: dataUse,
              backgroundColor:"rgb( 75, 192, 192)",
              borderWidth: 1,
            },
            {
              label: `# ${area}`,
              data: dataUse,
              backgroundColor: "rgb(239, 227, 226)",
            },
          ],
        }}
        width={400}
        height={200}
        options={{
          maintainAspectRatio: false,
        }}
      />}
      </>
        
    );
}

export default GraphComponent;