
import { useState } from "react";
import axios from "axios";
// import { Link } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import Swal from 'sweetalert2'
import "./forms.css"


const Register = ()=>{
  const history = useHistory();
  const [error, setError] = useState(false);
  const [username, setUsername] = useState("");
  const [first_name, setFirstName] = useState("");
  const [last_name, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [logUrl, setLogUrl] = useState("");
  const [success, setSuccess] = useState(false);
  const [user, setUser] = useState(null);
  const [passwordConfirm, setPasswordConfirm] = useState(null);
  const [passwordNotMatch, setPasswordNotMatch] = useState(null);
 

  const handleSubmit = async (e) => {
    //it prevents the page from refreshing
    e.preventDefault();

    if(password === passwordConfirm){
      setError(false);
    
      try {
        const res = await axios.post("https://evangelism.thecophq.org/api/auth/local/register", {
          first_name,
          last_name,
          username,
          email,
          password,
        });
        res.data && window.location.replace("/login");
        setLogUrl("https://www.google.com/");
        sendMail(first_name, last_name, email, username, password, logUrl);
        Swal.fire({
          title: 'Suucessful',
          text: 'Login successfully',
          icon: 'success',
          showCancelButton:false,
          timer:2000
        });
        history.push("/users");
      } catch (err) {
        setError(true);
        Swal.fire({
          title: 'Error',
          text: 'Registration failled',
          icon: 'error',
          showCancelButton:false,
          timer:2000
        });
      }
    }else{
      setPasswordNotMatch("Make sure password and Confirm password are the same");
    }
    
  }

 

  function sendMail(firstName, lastName, email, username, password, logUrl) {
    window.Email.send({
       SecureToken : "bbd218d4-61bd-46c1-9f0e-0e918acc9249",
       To : email,
       From : "evangelism@thecophq.org",
       Subject : `Welcome, ${firstName} ${lastName}`,
      Body: `Hello, You have been registered with the COP Evangelism Ministry. Find below login details; Username: ${username} Temporal Password: ${password}. Ensure that you update the login credentials before use. Thank you. The CoP Evangelism Team`
    }).then(

  message => setSuccess(true)  
  );
  }

    return(
      <div  style={{marginTop:"10rem"}}>
      <div className="card card-form mx-auto" >
      <div className="card-body">
        <h5 className="card-title text-center fw-bold">Registration Form</h5>
           
          <form className="registerForm" onSubmit={handleSubmit}> 
          <div className="my-2">
          <input type="text" className="form-control py-2" id="exampleFormControlInput1" placeholder="Enter your first name" required
            onChange={e=>setFirstName(e.target.value)}/>
          </div>
          <div className="my-2">
         
          <input type="text" className="form-control py-2" id="exampleFormControlInput2" placeholder="Enter your last name" required
            onChange={e=>setLastName(e.target.value)}/>
          </div>
          <div className="my-2">
          
          <input type="text" className="form-control py-2" id="exampleFormControlInput3" placeholder="Enter your username" required
            onChange={e=>setUsername(e.target.value)}/>
          </div>
          <div className="my-2">
          
          <input type="email" className="form-control py-2" id="exampleFormControlInput4" placeholder="Enter your email" required
            onChange={e=>setEmail(e.target.value)}/>
          </div>
          <div className="my-2">
          
          <input type="password" className="form-control py-2" id="exampleFormControlInput5" placeholder="Enter your password" required 
            onChange={e=>setPassword(e.target.value)}/>
          </div>

          <div className="my-2">
          
          <input type="password" className="form-control py-2" id="exampleFormControlInput6" placeholder="Confirm your password" required 
            onChange={(e)=>{setPasswordConfirm(e.target.value)}}/>
          </div>
            {passwordConfirm && (password !== passwordConfirm ) && <span className="text-danger text-center">Passwords do not match</span>}
            {passwordNotMatch && <div className="text-danger">{passwordNotMatch}</div>}
            <button className="btn btn-info" type="submit">Register</button>
            {/* <div className="text-center"><span>Already have an account? </span><Link className="ps-3" to="/admin/login">SignIn here</Link></div> */}
        </form>
        
            </div>
    </div>
    </div>   
    );
};

export default Register;