import axios from 'axios'
import EvangelistCount from 'components/EvangeslistCount'
import GraphDashboard from 'components/GraphDashboard'
import moment from 'moment'
import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
// reactstrap components
import {
  Card,
  CardBody,
  CardFooter,
  CardTitle,
  Col,
  Container,
  Row,
} from 'reactstrap'
import { Context } from '../context/Context'

function Dashboard() {
  const { user, dispatch } = useContext(Context)
  const [users, setUsers] = useState(null)
  const [prayers, setPrayers] = useState(null)
  const [converts, setConverts] = useState(null)
  const [hotlines, setHotlines] = useState(null)
  const [lastFive, setLastFive] = useState(null)
  const [error1, setError1] = useState(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    let source = axios.CancelToken.source()

    //fetching prayers
    getData('prayers', source)

    //fetching prayers
    getData('users', source)

    //fetching prayers
    getData('reports/converts', source)

    //fetching counsiling
    getData('hotlines', source)

    //get last five prayer
    getData('prayers/?_limit=5&_sort=created_at:DESC', source)

    // setIsLoading(false);
    return () => {
      source.cancel()
    }
  }, [])

  function getData(url, source) {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios
      .get(`https://evangelism.thecophq.org/api/${url}`, config, {
        CancelToken: source.token,
      })
      .then((result) => {
        switch (url) {
          case 'users':
            setUsers(result.data)
            break
          case 'reports/converts':
            setConverts(result.data)
            break
          case 'prayers':
            setPrayers(result.data)
            break
          case 'hotlines':
            setHotlines(result.data)
            break
          case 'prayers/?_limit=5&_sort=created_at:DESC':
            setLastFive(result.data)
            break
        }
        setIsLoading(false)
        setError1(null)
      })
      .catch((err) => {
        if (axios.isCancel(err)) {
          // console.log("abortion")
        } else {
          setError1('An error has been encountered, please try again later')
        }
      })
  }

  return (
    <>
      {error1 && (
        <div
          className="alert alert-danger"
          role="alert"
          style={{ marginTop: '15rem' }}
        >
          <p>{error1}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center" style={{ marginTop: '15rem' }}>
          <div
            className="spinner-border text-info text-center"
            role="status"
            style={{ display: 'inline-block' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
          <div
            className="spinner-border text-info text-center"
            role="status"
            style={{ display: 'inline-block' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
          <div
            className="spinner-border text-info text-center"
            role="status"
            style={{ display: 'inline-block' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      <div className="content">
        <p className="fw-bold">
          Welcome, {user.user.first_name} {user.user.last_name}
        </p>
        {/* <p className="fw-bold">{user.jwt}</p> */}
        {users && prayers && converts && lastFive && (
          <>
            <Row>
              {/* {countConvertByEvangelist} */}
              <Col className="col-lg-3 col-md-5 col-sm-6 col-10">
                <Card className="card-stats">
                  <CardBody>
                    <Row>
                      <Col md="4" xs="5">
                        <div className="icon-big text-center icon-warning">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="50"
                            height="50"
                            fill="currentColor"
                            className="bi bi-people-fill"
                            viewBox="0 0 16 16"
                            style={{ color: '#118372' }}
                          >
                            <path d="M7 14s-1 0-1-1 1-4 5-4 5 3 5 4-1 1-1 1H7zm4-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                            <path
                              fillRule="evenodd"
                              d="M5.216 14A2.238 2.238 0 0 1 5 13c0-1.355.68-2.75 1.936-3.72A6.325 6.325 0 0 0 5 9c-4 0-5 3-5 4s1 1 1 1h4.216z"
                            />
                            <path d="M4.5 8a2.5 2.5 0 1 0 0-5 2.5 2.5 0 0 0 0 5z" />
                          </svg>
                        </div>
                      </Col>
                      <Col md="8" xs="7">
                        <div className="numbers">
                          <p className="card-category">Converts</p>
                          <CardTitle tag="p">{converts.length}</CardTitle>
                          <p />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                  <CardFooter>
                    <hr />
                    <div className="stats">
                      <i className="fas fa-sync-alt" /> Total Number Of Converts
                    </div>
                  </CardFooter>
                </Card>
              </Col>
              <Col className="col-lg-3 col-md-5 col-sm-6 col-10">
                <Card className="card-stats">
                  <CardBody>
                    <Row>
                      <Col md="4" xs="5">
                        <div className="icon-big text-center icon-warning">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="50"
                            height="50"
                            fill="currentColor"
                            className="bi bi-person-plus"
                            viewBox="0 0 16 16"
                            style={{ color: 'teal' }}
                          >
                            <path d="M6 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H1s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C9.516 10.68 8.289 10 6 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z" />
                            <path
                              fillRule="evenodd"
                              d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"
                            />
                          </svg>
                        </div>
                      </Col>
                      <Col md="8" xs="7">
                        <div className="numbers">
                          <p className="card-category">Counsellers</p>
                          <CardTitle tag="p">0</CardTitle>
                          <p />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                  <CardFooter>
                    <hr />
                    <div className="stats">
                      <i className="far fa-calendar" /> Total Number of Request
                      for Counsel
                    </div>
                  </CardFooter>
                </Card>
              </Col>
              <Col className="col-lg-3 col-md-5 col-sm-6 col-10">
                <Card className="card-stats">
                  <CardBody>
                    <Row>
                      <Col md="4" xs="5">
                        <div className="icon-big text-center icon-warning">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="50"
                            height="50"
                            fill="currentColor"
                            className="bi bi-book"
                            viewBox="0 0 16 16"
                            style={{ color: '#f0750c' }}
                          >
                            <path d="M1 2.828c.885-.37 2.154-.769 3.388-.893 1.33-.134 2.458.063 3.112.752v9.746c-.935-.53-2.12-.603-3.213-.493-1.18.12-2.37.461-3.287.811V2.828zm7.5-.141c.654-.689 1.782-.886 3.112-.752 1.234.124 2.503.523 3.388.893v9.923c-.918-.35-2.107-.692-3.287-.81-1.094-.111-2.278-.039-3.213.492V2.687zM8 1.783C7.015.936 5.587.81 4.287.94c-1.514.153-3.042.672-3.994 1.105A.5.5 0 0 0 0 2.5v11a.5.5 0 0 0 .707.455c.882-.4 2.303-.881 3.68-1.02 1.409-.142 2.59.087 3.223.877a.5.5 0 0 0 .78 0c.633-.79 1.814-1.019 3.222-.877 1.378.139 2.8.62 3.681 1.02A.5.5 0 0 0 16 13.5v-11a.5.5 0 0 0-.293-.455c-.952-.433-2.48-.952-3.994-1.105C10.413.809 8.985.936 8 1.783z" />
                          </svg>
                        </div>
                      </Col>
                      <Col md="8" xs="7">
                        <div className="numbers">
                          <p className="card-category"> Prayers</p>
                          <CardTitle tag="p">{prayers.length}</CardTitle>
                          <p />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                  <CardFooter>
                    <hr />
                    <div className="stats">
                      <i className="far fa-clock" />
                      Total Number of Prayer Requests
                    </div>
                  </CardFooter>
                </Card>
              </Col>
              <Col className="col-lg-3 col-md-5 col-sm-6 col-10">
                <Card className="card-stats">
                  <CardBody>
                    <Row>
                      <Col md="4" xs="5">
                        <div className="icon-big text-center icon-warning">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="50"
                            height="50"
                            fill="currentColor"
                            className="bi bi-people"
                            viewBox="0 0 16 16"
                            style={{ color: '#4e62f6' }}
                          >
                            <path d="M15 14s1 0 1-1-1-4-5-4-5 3-5 4 1 1 1 1h8zm-7.978-1A.261.261 0 0 1 7 12.996c.001-.264.167-1.03.76-1.72C8.312 10.629 9.282 10 11 10c1.717 0 2.687.63 3.24 1.276.593.69.758 1.457.76 1.72l-.008.002a.274.274 0 0 1-.014.002H7.022zM11 7a2 2 0 1 0 0-4 2 2 0 0 0 0 4zm3-2a3 3 0 1 1-6 0 3 3 0 0 1 6 0zM6.936 9.28a5.88 5.88 0 0 0-1.23-.247A7.35 7.35 0 0 0 5 9c-4 0-5 3-5 4 0 .667.333 1 1 1h4.216A2.238 2.238 0 0 1 5 13c0-1.01.377-2.042 1.09-2.904.243-.294.526-.569.846-.816zM4.92 10A5.493 5.493 0 0 0 4 13H1c0-.26.164-1.03.76-1.724.545-.636 1.492-1.256 3.16-1.275zM1.5 5.5a3 3 0 1 1 6 0 3 3 0 0 1-6 0zm3-2a2 2 0 1 0 0 4 2 2 0 0 0 0-4z" />
                          </svg>
                        </div>
                      </Col>
                      <Col md="8" xs="7">
                        <div className="numbers">
                          <p className="card-category">Users</p>
                          <CardTitle tag="p">{users.length}</CardTitle>
                          <p />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                  <CardFooter>
                    <hr />
                    <div className="stats">
                      <i className="fas fa-sync-alt" /> Total Number of Users
                    </div>
                  </CardFooter>
                </Card>
              </Col>
            </Row>
            <hr />
            <Row>
              <div className="col-lg-8 col-md-8 col-sm-11 col-10 mx-1">
                <div className="card p-2">
                  <div className="card-header">
                    <h3>The last 5 prayers requested</h3>
                  </div>
                  <table className="table table-striped">
                    <thead className="border-top">
                      <tr>
                        {/* <th scope="col">Number</th> */}
                        <th scope="col">Requester Name</th>
                        <th scope="col">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {lastFive.map((value, index) => {
                        return (
                          // <Link to={`convert-details/${value._id}`}>
                          <tr key={index}>
                            <td>{value.convert_name}</td>
                            <td>{value.title}</td>
                            <td>
                              {moment(value.createdAt).format('YYYY-MM-DD')}
                            </td>
                            <td>
                              <Link to={`/admin/prayer-details/${value.id}`}>
                                <button
                                  type="button"
                                  className="btn btn-outline-info btn-sm"
                                >
                                  Details
                                </button>
                              </Link>
                            </td>
                          </tr>
                          // </Link>
                        )
                      })}
                    </tbody>
                  </table>
                </div>
              </div>
              <div className="col-lg-3 col-md-3 col-sm-10 col-10">
                <EvangelistCount data={users} />
              </div>
            </Row>
          </>
        )}
      </div>
      <hr className="mx-3 fw-bold" />
      <Container className="mx-2">
        <div className="p-3">
          {converts && (
            <GraphDashboard
              data={converts}
              area="# Converts"
              periodType="monthly"
            />
          )}
        </div>
      </Container>
    </>
  )
}

export default Dashboard
