import React from "react";

// reactstrap components
import axios from "axios";
import { useEffect, useState } from "react";
import { useLocation } from "react-router";
import 'react-table/react-table.css'
import PrayerComponent from "./PrayerComponent";
import { useContext } from "react";
import { Context } from "../context/Context";


function Prayers() {
  const { user, dispatch } = useContext(Context);
  const [prayers, setPrayers] = useState([]);
  const {search} = useLocation();
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // console.log("prayers",search)

    //fetch posts
  useEffect(() => {

        const config = {
          headers: { Authorization: `Bearer ${user.jwt}` }
        };
        axios.get("https://evangelism.thecophq.org/api/prayers", config)
              .then(result=>{
                  setPrayers(result.data);
                  setError(null);
                  setIsLoading(false);
              })
              .catch(err=>{
                setError("An error has been encountered, please try again later");
                setIsLoading(false);
              });
    }, [search]);

  return (
      <div className="content">
        {error && (<div className="alert alert-danger" role="alert">
                <p>{error}</p>
      </div>)}
      {isLoading && <div className="text-center">
        <div className="spinner-border text-info " role="status" style={{marginTop:"90px"}}>
          <span className="visually-hidden">Loading...</span>
        </div>
        </div>}
      {(prayers && !isLoading) && <PrayerComponent data={prayers} title="Prayers"/>}
      
      </div>
      );
  
}

export default Prayers;