import { useEffect, useState } from "react";
import { Container, Accordion, Row, Col } from "react-bootstrap";
import { useContext } from "react";
import axios from "axios";
import Select from "react-select";
import { Context } from "../context/Context";
import GraphsDashboad from "../components/GraphsDashboad";


const Analyse = ()=>{
     const { user, dispatch } = useContext(Context);
  const [areas, setAreas] = useState(null);
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedOption, setSelectedOption] = useState(null);
  const [selectedOption1, setSelectedOption1] = useState(null);

  useEffect(() => {
    //get all areas
    getData("districts");

    //get all converts
    getData("converts");
  }, []);

    function getData(url) {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };

    if (url === "districts") {
      axios
        .get(`https://evangelism.thecophq.org/api/${url}`, config)
        .then((result) => {
          // console.log(result.data)
          let temp = [];

          for (let area of result.data) {
            const option = { label: area.name, value: area.name };
            temp.push(option);
          }
          setAreas(temp);
          //   setIsLoading(false);
        })
        .catch((err) => {

          //   setError('Could not fetched data from that resource');
          //   setIsLoading(false)
        });
    } else {
      axios
        .get(`https://evangelism.thecophq.org/api/reports/${url}`, config)
        .then((result) => {
          setData(result.data);
           setError(null); setIsLoading(false);
        })
        .catch((err) => {
          setError("An error occured, please try again later");
            setIsLoading(false);
        });
    }
  }
return (
<Container className="mt-5">
      <Row className="pt-5">
        <Col className="col-lg-2 col-md-2 col-sm-5 col-10 db bg-light text-dark">
          <Accordion defaultActiveKey="0" className="mb-3">
            <Accordion.Item>
              <Select
                id="select-first-item"
                value={selectedOption}
                onChange={(value) => {
                  setSelectedOption(value);
                }}
                options={areas}
                placeholder="First Area"
                isClearable={false}
              />
            </Accordion.Item>
          </Accordion>
        </Col>
         <Col className="col-lg-2 col-md-2 col-sm-5 col-10 db bg-light text-dark">
          <Accordion defaultActiveKey="0" className="mb-3">
            <Accordion.Item>
              <Select
                id="select-second-item"
                value={selectedOption1}
                onChange={(value) => {
                  setSelectedOption1(value);
                }}
                options={areas}
                placeholder="Second Area"
                isClearable={false}
              />
            </Accordion.Item>
          </Accordion>
        </Col>
        <Col className="col-lg-8 col-md-8 col-sm-7 col-10">
          {(data && selectedOption && selectedOption1) ?
          (selectedOption === selectedOption1 ) ? <div className="alert alert-warning" role="alert">
          Choose two different districts in order to be able to compare them
        </div>: 
          <GraphsDashboad data={data} area={selectedOption} area1={selectedOption1} />:
          <p>Select two areas and then do your analyse</p>}
          {/* <h2>Diplay the graph over here</h2> */}
        </Col>
      </Row>
    </Container>
);
}

export default Analyse;