import axios from "axios";
import { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import { useParams } from "react-router";
import { useContext } from "react";
import { Context } from "../context/Context";
import { Link } from "react-router-dom";
import moment from "moment";


const UserDetails = () => {
  const { user, dispatch } = useContext(Context);
  const { id } = useParams();
  const [prayer, setPrayer] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {

    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };

    axios
      .get(`https://evangelism.thecophq.org/api/converts/${id}`, config)
      .then((result) => {
        setPrayer(result.data);
        setError(null);
        setIsLoading(false);
      })
      .catch((err) => {
        setError("An error has been encounted, please try again later");
        setIsLoading(false);
      });

  },[id]);

  return (
    <Container className="mt-5">
      {error && (<div className="alert alert-danger" role="alert">
                <p>{error}</p>
      </div>)}
      {isLoading && <div className="text-center">
        <div className="spinner-border text-info " role="status" style={{marginTop:"60px"}}>
          <span className="visually-hidden">Loading...</span>
        </div>
        </div>}
      {prayer && (
        <div className="pt-5" >
          <div className="card pt-2" style={{width: "22rem", marginLeft:"30%"}}>
            <div className="pt-3 text-center">
            <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" className="bi bi-person-circle" viewBox="0 0 16 16" style={{color:"#a8aeac"}}>
            <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z"/>
            <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"/>
            </svg>
            </div>
            <div className="card-body">
              {/* <h5 className="card-title">Prayer request details</h5> */}
              <div className="py-2 " style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>First Name:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.first_name}</div>
              </div>
              <div className="py-2 " style={{backgroundColor:"#f9fafa"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>Last Name:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.last_name}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>Phone:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.phone}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f9fafa"}}>
                <div className="px-3 " style={{"display":"inline-block"}}>Email:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.email}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>Date won:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.date_won}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f9fafa"}}>
                <div className="px-3 " style={{"display":"inline-block"}}>Area:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>Area Name</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 text-align-start" style={{"display":"inline-block"}}>District:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.district.name}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f9fafa"}}>
                <div className="px-3 " style={{"display":"inline-block"}}>Age:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{prayer.age}</div>
              </div>
              <div className="py-2" style={{backgroundColor:"#f5f4ee"}}>
                <div className="px-3 " style={{"display":"inline-block"}}>Registration Date:</div>
                <div className="px-3 fw-bold" style={{"display":"inline-block"}}>{moment(prayer.createdAt).format('YYYY-MM-DD')}</div>
              </div>
              
            </div>
            
            <div className="card-body text-center">
              <Link to="/admin/converts" className="card-link">
                  <button type="button" className="btn btn-outline-info">Go back to convert page</button>
              </Link>
            </div>
          </div>
        </div>
      )} 
    </Container>
  );
};

export default UserDetails;
