import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { useContext } from "react";
import { Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { Context } from "../context/Context";

const Media = () => {
    const photo = useRef();
    const [imageChosen, setImageChosen] = useState(false);
    const { user, dispatch } = useContext(Context);
    const [data, setData] = useState(null);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(()=>{
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` }
        };

        axios.get("http://20.97.192.152:1338/upload/files",config)
            .then(result=>{
                // console.log(result.data)
                setData(result.data);
                setIsLoading(false);
                setError(null);
            })
            .catch(err=>{
                setError("Could not get images from the server, please try again later");
                setIsLoading(false);
            });
        
    },[imageChosen]);

    const deleteImage = (imageId)=>{
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` }
        };

        axios.delete(`http://20.97.192.152:1338/upload/files/${imageId}`,config)
            .then(result=>{
                setImageChosen(!imageChosen);
                Swal.fire('Image deleted successfully!', '', 'success')
            })
            .catch(err=>{
                Swal.fire('An error occured, try again later!', '', 'error')
            });
    }
    const handleDeleteImage = (imageId, imagePath)=>{
        Swal.fire({
            imageUrl:imagePath,
            imageWidth:300,
            imageHeight:160,
            text: 'Click on delete to remove this image',
            showCloseButton: true,
            showCancelButton: true,
            confirmButtonText: 'Delete',
            denyButtonText: `Don't save`,
          })
          .then(result=>{
            if (result.isConfirmed) {
                deleteImage(imageId)
              }
          })
    }

    const handleImageSelected = (files)=>{
        console.log(files[0].type)
        if(files[0].type.includes('image')){
            const formData = new FormData();
        formData.append("files",files[0])
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` }
        };
        axios.post("http://20.97.192.152:1338/upload", formData, config)
            .then(result=>{
                setImageChosen(!imageChosen);
            })
            .catch(err=>{ Swal.fire({
                title: 'Error',
                text: 'Upload failled, please try again latter',
                icon: 'error',
                showCancelButton:false,
                
            });
        });
        }else{
            Swal.fire({
                title: 'Error',
                text: 'The file must be an image',
                icon: 'error',
                showCancelButton:false,
            });
        }

        
    };
   
  return (
    <div className="media" style={{ marginTop: "8rem" }}>
        <div className="card mx-auto" style={{width: "30rem"}}>
            
        {/* {imageChosen && <img src="C:\fakepath\icon1.png" className="card-img-top" /> } */}
  <div className="card-body">
  <div className="mb-3">
        <label htmlFor="formFileMultiple" className="form-label">
          Choose media
        </label>
        <input
            
          className="form-control"
          type="file"
          id="formFileMultiple"
          onChange={(event)=>{ 
              if(event.target.files){
                handleImageSelected(event.target.files)
              }
          }}
        />
      </div>
  </div>
</div>
     <hr/>
     <div className="mt-4">
       <div className="container">
        {error && (<div className="alert alert-danger" role="alert">
                    <p>{error}</p>
        </div>)}
        {isLoading && <div className="text-center">
            <div className="spinner-border text-info " role="status" style={{marginTop:"60px"}}>
            <span className="visually-hidden">Loading...</span>
            </div>
        </div>}
        {data && <>
            <Row>
                {data.map((item, index)=>{
                    return(
                        
                        <div className="col-5 col-sm-4 col-md-3 col-lg-3 p-1" key={index}>
                            <button style={{background:"white", border:"none"}} onClick={()=>{
                                handleDeleteImage(item.id, `http://20.97.192.152:1338${item.url}`);
                            }}>
                            <img src={`http://20.97.192.152:1338${item.url}`} alt={item.name} height={130} 
                            style={{minWidth:"100%", objectFit:"cover", width:350}}
                            />
                            </button>
                        </div>
                         
                    )
                })}
          
           </Row>
        </>}
        </div> 
    </div>  
    </div>
  );
};

export default Media;
