import { Container, Accordion, Row, Col } from "react-bootstrap";
// import Achievment from "../components/Achievment";
import axios from "axios";
import { useEffect, useState } from "react";
import Select from "react-select";
import { useContext } from "react";
import { Context } from "../context/Context";
import GraphsDashboad from "./GraphsDashboad";

const Reports = () => {
  const { user, dispatch } = useContext(Context);
  const [areas, setAreas] = useState(null);
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedOption, setSelectedOption] = useState(null);

  useEffect(() => {
    //get all areas
    getData("districts");

    //get all converts
    getData("converts");
  }, []);

  function getData(url) {
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };

    if (url === "districts") {
      axios
        .get(`https://evangelism.thecophq.org/api/${url}`, config)
        .then((result) => {
          // console.log(result.data)
          let temp = [];

          for (let area of result.data) {
            const option = { label: area.name, value: area.name };
            temp.push(option);
          }
          setAreas(temp);
          //   setIsLoading(false);
        })
        .catch((err) => {

          //   setError('Could not fetched data from that resource');
          //   setIsLoading(false)
        });
    } else {
      axios
        .get(`https://evangelism.thecophq.org/api/reports/${url}`, config)
        .then((result) => {
          setData(result.data);
           setError(null); setIsLoading(false);
        })
        .catch((err) => {
          setError("An error occured, please try again later");
            setIsLoading(false);
        });
    }
  }
  return (
    <Container className="mt-5">
      <Row className="pt-5">
        <Col className="col-lg-3 col-md-3 col-sm-5 col-10 db bg-light text-dark">
          <Accordion defaultActiveKey="0" className="mb-3">
            <Accordion.Item>
              <Select
                id="select-item"
                value={selectedOption}
                onChange={(value) => {
                  setSelectedOption(value);
                }}
                options={areas}
                placeholder="Select District"
                isClearable={false}
              />
            </Accordion.Item>
          </Accordion>
        </Col>
        <Col className="col-lg-8 col-md-8 col-sm-7 col-10">
          {error && (
            <div className="alert alert-danger" role="alert">
              <p>{error}</p>
            </div>
          )}
          {isLoading && (
            <div className="text-center">
              <div
                className="spinner-border text-info "
                role="status"
                style={{ marginTop: "60px" }}
              >
                <span className="visually-hidden">Loading...</span>
              </div>
            </div>
          )}
          {data && <GraphsDashboad data={data} area={selectedOption} />}
        </Col>
      </Row>
    </Container>
  );
};

export default Reports;
